// Animation
@-webkit-keyframes rotateHint {
  0% {
    -webkit-transform: rotate(0deg);
    opacity: 0.2;
  }
  50% {
    -webkit-transform: rotate(90deg);
    opacity: 1;
  }
  100% {
    -webkit-transform: rotate(90deg);
    opacity: 0;
  }
}
@keyframes rotateHint {
  0% {
    transform: rotate(0deg);
    opacity: 0.2;
  }
  50% {
    transform: rotate(90deg);
    opacity: 1;
  }
  100% {
    transform: rotate(90deg);
    opacity: 0;
  }
}

@keyframes lineEye {
  0% {
    width: 8px;
    height: 4px;
    border-radius: 20px;
  }
  5% {
    width: 8px;
    height: 3px;
    border-radius: 20px;
  }
  10% {
    width: 8px;
    height: 4px;
    border-radius: 20px;
  }
}

@keyframes circleEye {
  0% {
    width: 6px;
    height: 6px;
    border-radius: 6px;
  }
  5% {
    width: 6px;
    height: 4px;
    border-radius: 100px;
  }
  10% {
    width: 6px;
    height: 6px;
    border-radius: 6px;
  }
}

@keyframes upDownMove {
	0% {
		bottom: 25px;
	}
	50% {
		bottom: 18px;
	}
	to {
		bottom: 25px;
	}
}

@keyframes pulse {
  from {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }

  50% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1);
    transform: scale3d(1.1, 1.1, 1.1);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}


// animate.css
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}